* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  background-image: url(../images/82073184.png);
  background-attachment: fixed;
  background-size: 500px;
}
body p {
  font-family: "Roboto Mono", monospace;
}

header {
  background-color: black;
  height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
nav img {
  height: 50px;
}
nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
  font-family: "Pixelify Sans", sans-serif;
  padding: 10px 20px;
  padding-left: 0;
}
h2,
h3,
h4 {
  font-family: "Pixelify Sans", sans-serif;
}
nav div {
  display: flex;
  align-items: center;
}
header h1 {
  font-size: 1rem;
  color: white;
  margin: 0;
}
nav ul {
  display: none;
}
main {
  margin: 90px 0;
}
footer {
  padding-top: 10px;
  background-color: black;
  height: 70px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  color: white;
  align-items: center;
  font-size: 12px;
}
footer a {
  color: white;
}
main ul {
  padding: 0;
}
footer p {
  margin: 0;
}
footer ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  width: 400px;
}
@media screen and (min-width: 768px) {
  header h1 {
    font-size: 3vh;
    color: white;
  }
  header .menu-icon {
    display: none;
  }
  nav ul {
    font-size: 1.5rem;
    display: flex;
    color: white;
    list-style: none;
    width: 70%;
    justify-content: space-around;
  }

  header {
    height: 15vh;
  }
  nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }
  nav img {
    height: 50px;
  }
  nav a {
    text-decoration: none;
    color: white;
  }
  nav li:hover {
    transform: scale(1.5);

    transition: 1s;
  }
  nav a:hover {
    color: #e19200;
  }
}
/*End of Style bases for body,header,footer*/
main {
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  width: 70%;
  height: 70vh;
}
main ul {
  font-family: "Pixelify Sans", sans-serif;
  font-size: 1.5rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
main a {
  text-decoration: none;
  color: white;
}
body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
}
.filler1 {
  height: 8vh;
  width: 100%;
}
.filler2 {
  width: 100%;
  height: 10vh;
}
