* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url("../82073184.5b388fda.png");
  background-size: 500px;
  background-attachment: fixed;
  margin: 0;
}

body p {
  font-family: Roboto Mono, monospace;
}

header {
  height: 70px;
  z-index: 1;
  background-color: #000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

nav img {
  height: 50px;
}

nav {
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px 10px 0;
  font-family: Pixelify Sans, sans-serif;
  display: flex;
}

h2, h3, h4 {
  font-family: Pixelify Sans, sans-serif;
}

nav div {
  align-items: center;
  display: flex;
}

header h1 {
  color: #fff;
  margin: 0;
  font-size: 1rem;
}

nav ul {
  display: none;
}

main {
  margin: 90px 0;
}

footer {
  height: 70px;
  z-index: 1;
  color: #fff;
  background-color: #000;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
  font-size: 12px;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

footer a {
  color: #fff;
}

main ul {
  padding: 0;
}

footer p {
  margin: 0;
}

footer ul {
  width: 400px;
  justify-content: space-around;
  list-style: none;
  display: flex;
}

@media screen and (min-width: 768px) {
  header h1 {
    color: #fff;
    font-size: 3vh;
  }

  header .menu-icon {
    display: none;
  }

  nav ul {
    color: #fff;
    width: 70%;
    justify-content: space-around;
    font-size: 1.5rem;
    list-style: none;
    display: flex;
  }

  header {
    height: 15vh;
  }

  nav {
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
  }

  nav img {
    height: 50px;
  }

  nav a {
    color: #fff;
    text-decoration: none;
  }

  nav li:hover {
    transition: all 1s;
    transform: scale(1.5);
  }

  nav a:hover {
    color: #e19200;
  }
}

main {
  color: #fff;
  width: 70%;
  height: 70vh;
  background-color: #000;
  flex-direction: column;
  display: flex;
}

main ul {
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-family: Pixelify Sans, sans-serif;
  font-size: 1.5rem;
  list-style: none;
  display: flex;
}

main a {
  color: #fff;
  text-decoration: none;
}

body {
  height: 100vh;
  flex-direction: column;
